import React from "react"
import { useStaticQuery, graphql } from "gatsby";
import Img from "gatsby-image";

import Layout from "../components/layout"
import SEO from "../components/seo"

// Components
import AboutLinks from "../components/AboutLinks/AboutLinks.component";
import TonisityProduct from "../components/TonisityProduct/TonisityProduct.component";

const About = () => {

  const data = useStaticQuery(graphql`
      query {
        aboutFolder: allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(about)/"}, frontmatter: {title: {ne: "About"}}}, limit: 2, sort: {fields: id, order: ASC}) {
          edges {
            node {
              frontmatter {
                title
                description
                productDesc
                productBenefits
                productPositive01
                productPositive02
                productPositive03
                productPositive04
                productBody
              }
              id
            }
          }
        }
        aboutPage: allMarkdownRemark(filter: {frontmatter: {title: {eq: "About"}}}) {
          edges {
            node {
              frontmatter {
                title
                description
                headImage {
                  childImageSharp {
                    fluid (maxWidth: 1920) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
      }
    `)

  return (
    <Layout>
      
      {
        data.aboutPage.edges.map(({node}) => (
          <div>
            <SEO title={ node.frontmatter.title } description={ node.frontmatter.description } />
            <Img fluid={ node.frontmatter.headImage.childImageSharp.fluid } />
          </div>
        ))
      }
      <AboutLinks />
      {
        data.aboutFolder.edges.map(({node}) => (
          <TonisityProduct 
            productTitle={node.frontmatter.title} 
            productDescription={node.frontmatter.description}
            productBenefits={node.frontmatter.productBenefits}
            productPositives01={node.frontmatter.productPositive01}
            productPositives02={node.frontmatter.productPositive02}
            productPositives03={node.frontmatter.productPositive03}
            productPositives04={node.frontmatter.productPositive04}
            productBody={node.frontmatter.productBody}
            productDesc={node.frontmatter.productDesc}
            productId={node.id}
          />
        ))
      }
    </Layout>
  )
}

export default About
